@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body *{
  font-family: 'Roboto', sans-serif;
}
h1{
  margin:0;
}
.App{
  max-width: 1150px;
  margin:0 auto;
}
ul{
  padding:0;
  margin:0;
  list-style-type: none;
}
/* menu*/

.logo{
  width:72px;
}
.navi{

display: flex;
}
.navi__element{
  margin: 0 16px;
  font-size: 16px;
  line-height: 24px;
}
.menu{
display: flex;
justify-content: space-between;
padding: 18px 0;
margin-top:22px;
align-items: center;
}
.calculatorContact{
  display: flex;
}
.calculatorContact__contact{
  box-shadow: 0 0 0 1px #ccd4de;
  border-radius:4px;
  font-size: 16px;
  font-weight: 400;
  padding: 7px 16px 9px;

}
.calculatorContact__calculator{
  box-shadow: 0 0 0 1px #00479a;
  border-radius:4px;
  background-color:#00479a;
  font-size: 16px;
  font-weight: 400;
  color:white;
  padding: 7px 16px 9px;
  margin-left: 7px;
}
/*banner*/
.banner{
  display: flex;
  margin-top:63px;
}
.banner-texts{
  display:flex;
  flex-direction:column;
  align-items: flex-start;
}
.banner-texts__header{
  font-size: 62px;
  line-height: 1.125;
  font-weight: 600;
  margin-bottom: .35em;
}
.banner-texts__subheader{
  font-size: 20px;
  line-height: 1.6;
  margin-bottom: 24px;
  font-weight: 400;
}
.banner-texts__calculator{
  box-shadow: 0 0 0 1px #0055b8;
  border-radius:4px;
  background-color:#0055b8;
  color:white;
  padding: 15px 16px 17px;
  font-size: 18px;
  font-weight: 500;
  margin: 8px 16px 8px 0;
  line-height: 24px;
}
.banner-texts__about{
  box-shadow: 0 0 0 1px #ccd4de;
  border-radius:4px;
  font-size: 18px;
  font-weight: 400;
  margin: 8px 16px 8px 0;
  padding: 15px 16px 17px;
  min-width: 176px;
  text-align: center;
}
